import { SETSCENE, SETSUBSCENE } from '../actions/scene'

const initialState = {
    _currentScene: 'IndexScene', // 
    _currentSubScene: '',
};

export default function scene(state = initialState, action) {
    switch(action.type) {
        case SETSCENE:
            console.log('SETSCENE:' + action._changeScene);
            return Object.assign({}, state, {
                _currentScene: action._changeScene
            });
        case SETSUBSCENE:
            console.log('SETSUBSCENE:' + action._changeSubScene);
            return Object.assign({}, state, {
                _currentSubScene: action._changeSubScene
            });
        default:
            return state;
    }
}



