import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { makeStyles } from '@material-ui/core/styles';
import { SetSubScene } from '../actions/scene';

import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'fixed',
        left: '0%',
        top: '0%',
        width: '100%',
        height: '100%'
    },
    contentsTitle: {
        position: 'absolute',
        left: '5%',
        top: '5%',
        fontSize: '250%',
        textShadow: '2px 2px 5px black',
        color: 'red'
    },
    contentsFailText: {
        position: 'absolute',
        left: '5%',
        top: '20%',
        fontSize: '250%',
        textShadow: '2px 2px 5px black',
        color: 'white'
    },
    retryButton: {
        position: 'absolute',
        left: '60%',
        top: '5%',
        width: '30%',
        height: '20%',
        fontSize: '150%',
        opacity: 0.5
    },
    OKButton: {
        position: 'absolute',
        left: '60%',
        top: '35%',
        width: '30%',
        height: '20%',
        fontSize: '150%',
        opacity: 0.5
    },
}));

var _contentsTitle = '추가 기록 필요';
var _contentsFailText = 'SET';
var _retryText = '재도전';
var _OKText = '확인';
export const ResultFailure = ({ SetSubScene, _currentSubScene, failureCount, onOKClick, onRetryClick}) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
        <span className={classes.contentsTitle}>{_contentsTitle}</span>
        <span className={classes.contentsFailText}>{failureCount + _contentsFailText}</span>
        <Button className={classes.retryButton} onClick={onRetryClick} variant="contained" color="secondary">{_retryText}</Button>
        <Button className={classes.OKButton} onClick={onOKClick} variant="contained" color="secondary">{_OKText}</Button>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _currentSubScene: state.scene._currentSubScene
});
const mapDispatchToProps = { SetSubScene };

export default connect(mapStateToProps, mapDispatchToProps)(ResultFailure);
