export const SETSCENE = 'SETSCENE';
export function setScene(changeScene) {
    return {
        type: SETSCENE,
        _changeScene: changeScene
    };
}

export const SETSUBSCENE = 'SETSUBSCENE';
export function SetSubScene(changeSubScene) {
    return {
        type: SETSUBSCENE,
        _changeSubScene: changeSubScene
    };
}
