import React, { Component } from 'react'
import { connect } from 'react-redux';
import clsx from 'clsx';

import * as posenet from '@tensorflow-models/posenet'
import { drawKeyPoints, drawSkeleton, drawSkeletonDegree } from './PosenetUtils'
import { CanvasFPS_Update, CanvasFPS_Render } from './../utils/CanvasFPS'
import { SetPosenetPose, ClosePosenet } from '../../actions/posenet'

const _miniVideoAreaBase = {
  zIndex: '0',
  position: 'fixed',
  right: '0%',
  bottom: '0%',
  width: '30%',
}
const _miniVideoArea = {
  height: '40%'
}
const _miniVideoTextArea = {
  height: '40%'
}

const _fullVideoAreaBase = {
  zIndex: '0',
  position: 'fixed',
  left: '0%',
  top: '0%',
  height: '100%'
}
const _fullVideoArea = {
  width: 'auto'
}
const _fullVideoTextArea = {
  width: '100%'
}

const contentsTextBase = {
  position: 'absolute',
  zIndex: '1',
  left: '0%',
  top: '0%',
  width: '100%',
  height: '100%',
  margin: '0 auto',
  textShadow: '2px 2px 5px black',
  color: 'white'
}
const _contentsLoading = {
  textAlign: 'left',
  fontSize: '150%',
  //backgroundColor: '#FF000088',
}

const _miniContentsLoading = {
  textAlign: 'center',
  fontSize: '80%',
  //backgroundColor: '#FF000088',
}

const _contentsScaning = {
  textAlign: 'right',
  fontSize: '130%',
  //backgroundColor: '#0000FF88',
}

const _miniContentsScaning = {
  textAlign: 'center',
  fontSize: '80%',
  //backgroundColor: '#0000FF88',
}

const canvasStyle = {
  position: "fixed",
  left: "0%",
  //top: "0%",
  width: "100%",
  height: "auto",
  top: "50%",
  transform: 'translate(0%, -50%)',
  zIndex: '-1'
}

// 최적화로 이미 종료 되었지만 이미지 Pose 체크를 하는 경우를 위해서 flag 추가
var _distroyComponent = false;
class PoseNetCamera extends Component {
  static defaultProps = {
    flipHorizontal: true,
    algorithm: 'single-pose',
    showMiniVideo: false,
    showVideo: true,
    showDebugVideo: false,
    showSkeleton: false,
    showPoints: false,
    showSkeletonDegree: false,
    minPoseConfidence: 0.7,
    minPartConfidence: 0.5,
    maxPoseDetections: 2,
    nmsRadius: 20,
    outputStride: 16,
    imageScaleFactor: 0.5,
    skeletonColor: '#ffadea',
    skeletonDegreeColor: '#ff0000',
    skeletonLineWidth: 6,
    loadingText: 'Loading...please be patient...'
  }

  constructor(props) {
    super(props, PoseNetCamera.defaultProps);
    this.stopCamera = this.stopCamera.bind(this);
    console.log('PoseNetCamera init');
  }

  getCanvas = elem => {
    this.canvas = elem
  }

  getVideo = elem => {
    this.video = elem;
  }

  async componentDidMount() {
    _distroyComponent = false;
    try {
      await this.setupCamera()
    } catch (error) {
      throw new Error(
        'This browser does not support video capture, or this device does not have a camera'
      )
    }
/*
    try {
      this.posenet = await posenet.load()
    } catch (error) {
      throw new Error('PoseNetCamera failed to load')
    } finally {
      setTimeout(() => {
        this.setState({ loading: false })
      }, 200)
    }
    this.detectPose();*/
  }

  componentWillUnmount() {
    _distroyComponent = true;
    console.log('PoseNetCamera componentWillUnmount');
    this.stopCamera();
  }

  async setupCamera() {
    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      throw new Error(
        'Browser API navigator.mediaDevices.getUserMedia not available'
      )
    }

    //video.srcObject = stream;
    // return new Promise(resolve => {
    //   video.onloadedmetadata = () => {
    //     video.play()
    //     resolve(video)
    //   }
    // })
    this.playCamera();
    this.updateCamera();
  }


  async playCamera() {
    if (window.stream) {
        window.stream.getTracks().forEach(track => {
            track.stop();
        });
    }

    const video = this.video;
    //video.style.transform = 'rotatey(180deg)';

    // const videoSource = _videoCamneraList[_videoSelectIndex];
    // console.log('videoSource:' + videoSource);

    var screenWidth = 1080;//1280;//1920;//video.width;//window.innerWidth;
    var screenHeight = 1080;//720;//1080;//video.height;//window.innerHeight;

    var facingModeText = 'user';
    // if (_videoUserCameraFlag == false) {
    //     facingModeText = 'environment';
    //}
    const stream = await navigator.mediaDevices.getUserMedia({
      audio: false,
      video: {
        //deviceId: videoSource ? { exact: videoSource } : undefined,
        facingMode: facingModeText,
        frameRate: 60,
        width: {exact: screenWidth},
        height: {exact: screenHeight},
        aspectRatio: {
            exact: screenWidth / screenHeight
        }
      }
    })

    window.stream = stream;
    video.srcObject = stream;
    video.play();
}
  
  updateCamera() {
    const canvas = this.canvas;
    if (undefined == canvas) {
        return;
    }
    const canvasContext = canvas.getContext('2d')
    if (undefined == canvasContext) {
        return;
    }
    const video = this.video;
    if (undefined == video) {
        return;
    }
    //console.log('showVideo:' + this.props.showVideo);
    if (false == this.props.showVideo) {
        video.style.display = "none";
    }
    else {
        video.style.display = "block";
    }
    this.updateCameraFrame(canvasContext)
}

updateCameraFrame(canvasContext) {
    const video = this.video;
    const findUpdateCameraFrame = async () => {
        const canvas = this.canvas;
        if (true == this.props.showDebugVideo && undefined != canvas) {

            if (video.videoWidth != canvas.width || video.videoHeight != canvas.height) {
                canvas.width = video.videoWidth;
                canvas.height = video.videoHeight;
                canvasContext.width = video.videoWidth;
                canvasContext.height = video.videoHeight;
            }
            canvasContext.clearRect(0, 0, canvas.width, canvas.height);
            canvasContext.save();
            canvasContext.drawImage(video, 0, 0);//, video.videoWidth, video.videoHeight)
            canvasContext.restore();
        }

        if ('start' == this.props._closeCamera) {
            this.stopCamera();
            this.props.onCloseCamera('complete');
            return;
        }
        requestAnimationFrame(findUpdateCameraFrame)
    }
    findUpdateCameraFrame()
}

  stopCamera() {
    console.log('stopCamera call');
    const video = this.video;
    //console.log(video);
    if (undefined == video) {
      return;
    }
    video.pause();
    video.src = "";
    if (undefined != video.srcObject) {
      //video.srcObject.stop();
      video.srcObject.getTracks()[0].stop();
      video.srcObject = undefined;
    }
    console.log("video off");
  }

  detectPose() {
    const canvas = this.canvas
    if (undefined == canvas) {
      return;
    }
    const canvasContext = canvas.getContext('2d')
    if (undefined == canvasContext) {
      return;
    }

    const { showVideo } = this.props;
    const video = this.video;
    if (false == showVideo) {
      video.style.display = "none";
    }
    else {
      video.style.display = "block";
    }

    this.poseDetectionFrame(canvasContext)
  }

  poseDetectionFrame(canvasContext) {
    const {
      algorithm,
      imageScaleFactor,
      flipHorizontal,
      outputStride,
      minPoseConfidence,
      minPartConfidence,
      maxPoseDetections,
      nmsRadius,
      showDebugVideo,
      showPoints,
      showSkeleton,
      showSkeletonDegree,
      skeletonColor,
      skeletonDegreeColor,
      skeletonLineWidth
    } = this.props

    const posenetModel = this.posenet
    const video = this.video

    const findPoseDetectionFrame = async () => {

      if (_distroyComponent == true) {
        return;
      }
      try {

        var pose = {};
        pose.score = 0.5;
        this.props.onPosenetPose(pose);
        /*
        let poses = []
        switch (algorithm) {
          case 'multi-pose': {
            poses = await posenetModel.estimateMultiplePoses(
              video,
              imageScaleFactor,
              flipHorizontal,
              outputStride,
              maxPoseDetections,
              minPartConfidence,
              nmsRadius
            )
            break
          }
          case 'single-pose': {
            const pose = await posenetModel.estimateSinglePose(
              video,
              imageScaleFactor,
              flipHorizontal,
              outputStride
            )
            //SetPoseData(pose);
            this.props.onPosenetPose(pose);
            poses.push(pose)
            break
          }
        }
        */
/*
        const canvas = this.canvas;
        if (true == showDebugVideo && undefined != canvas) {
          // 
          canvasContext.clearRect(0, 0, canvas.width, canvas.height)
          CanvasFPS_Update();
          canvasContext.save();
          canvasContext.scale(-1, 1)
          canvasContext.translate(-canvas.width, 0)
          canvasContext.drawImage(video, video.left, video.top, video.left + canvas.width / 2, video.top + canvas.height)

          poses.forEach(({ score, keypoints }) => {
            if (score >= minPoseConfidence) {
              if (showPoints) {
                drawKeyPoints(
                  keypoints,
                  minPartConfidence,
                  skeletonColor,
                  canvasContext
                )
              }

              if (showSkeleton) {
                drawSkeleton(
                  keypoints,
                  minPartConfidence,
                  skeletonColor,
                  skeletonLineWidth,
                  canvasContext
                )
              }

              if (showSkeletonDegree) {
                drawSkeletonDegree(
                  keypoints,
                  minPartConfidence,
                  skeletonDegreeColor,
                  canvasContext
                )
              }
            }
          })
          canvasContext.restore();
          CanvasFPS_Render(canvasContext);
        }
        */
      } catch (error) {
        console.log('posenet detection error');
        // throw new Error(
        //   'This browser does not support video capture, or this device does not have a camera'
        // )
      }

      // setTimeout(function () {
      //   requestAnimationFrame(findPoseDetectionFrame);
      // }, 1000);
    }
    findPoseDetectionFrame()
  }

  getCameraScan = () => {
    var textStyle;
    if (true == this.props.showMiniVideo) {
      textStyle = Object.assign({}, contentsTextBase, _miniContentsLoading);
    } else {
      textStyle = Object.assign({}, contentsTextBase, _contentsLoading);
    }

    if (undefined != this.props._posenetPose) {
      return (undefined);
    }
    return (
      <span style={textStyle}>카메라 인식 중...</span>
    )
  }

  getCameraInvalid = () => {
    if (undefined == this.props._posenetPose) {
      return undefined;
    }
    if (this.props.minPoseConfidence < this.props._posenetPose.score) {
      return undefined;
    }

    var textStyle;
    if (true == this.props.showMiniVideo) {
      textStyle = Object.assign({}, contentsTextBase, _miniContentsScaning);
    } else {
      textStyle = Object.assign({}, contentsTextBase, _contentsScaning);
    }

    return (
      <span style={textStyle}>화면 안으로 들어오세요.</span>
    )
  }

  render() {
    var videoStyle;
    var videoTextStyle;
    if (true == this.props.showMiniVideo) {
      videoStyle = Object.assign({}, _miniVideoAreaBase, _miniVideoArea);
      videoTextStyle = Object.assign({}, _miniVideoAreaBase, _miniVideoTextArea);
    } else {
      videoStyle = Object.assign({}, _fullVideoAreaBase, _fullVideoArea);
      videoTextStyle = Object.assign({}, _fullVideoAreaBase, _fullVideoTextArea);
    }

    return (
      <div >
        <video id='videoCamera' style={videoStyle} src="" playsInline ref={this.getVideo} />
        <canvas style={canvasStyle} ref={this.getCanvas} />
        {/* <div style={videoTextStyle}>
          {this.getCameraInvalid()}
        </div>
        <canvas style={{ position: 'fixed', left: '0%', top: '0%', zIndex: '1' }} ref={this.getCanvas} /> */}
      </div>
    )
  }
}

let mapStateToProps = (state) => {
  return {
    _currentScene: state.scene._currentScene,
    _closePosenet: state.posenet._closePosenet,
    _posenetPose: state.posenet._posenetPose,
  };
}

let mapDispatchToProps = (dispatch) => {
  return {
    onPosenetPose: (poseData) => dispatch(SetPosenetPose(poseData)),
    onClosePosenet: (state) => dispatch(ClosePosenet(state))
  }
}

PoseNetCamera = connect(mapStateToProps, mapDispatchToProps)(PoseNetCamera);

export default PoseNetCamera;
