export const SETPOSENETPOSE = 'SETPOSENETPOSE';
export function SetPosenetPose(pose) {
    return {
        type: SETPOSENETPOSE,
        _posenetPose: pose
    };
}

export const CLOSEPOSENET = 'CLOSEPOSENET';
export function ClosePosenet(state) {
    console.log('ClosePosenet action call:' + state);
    return {
        type: CLOSEPOSENET,
        _closePosenet: state
    };
}

export const INVALIDPOSENET = 'INVALIDPOSENET';
export function InvalidPosenet(key) {
    //console.log('InvalidPosenet action call');
    return {
        type: INVALIDPOSENET,
        _invalidPoseKey: key
    };
}

export const INVALIDPOSENET_CLEAR = 'INVALIDPOSENET_CLEAR';
export function InvalidPosenetClear() {
    //console.log('InvalidPosenetClear action call');
    return {
        type: INVALIDPOSENET_CLEAR
    };
}

export const VALIDPOSENET = 'VALIDPOSENET';
export function ValidPosenet(key) {
    //console.log('ValidPosenet action call');
    return {
        type: VALIDPOSENET,
        _validPoseKey: key
    };
}
