import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import { SetMaxTime, SetCurrentTime } from '../actions/countdown';

import IconButton from "@material-ui/core/IconButton";
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import { green } from '@material-ui/core/colors';

import { EnableScreenMirror } from './utils/SendMessage';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%'
    },
    video: {
        position: 'relative',
        width: '100%',
        margin: '0'
    },
    rootFullScreen: {
        position: 'fixed',
        left: '0%',
        top: '0%',
        width: '100%',
        height: '100%'
    },
    videoFullScreen: {
        position: 'absolute',
        width: 'auto',
        height: '100%',
        left: '0%',
        top: '0%',
        margin: '0'
    },
    contentsTitle: {
        position: 'absolute',
        width: '100%',
        left: '0%',
        top: '0%',
        fontSize: '250%',
        textShadow: '2px 2px 5px black',
        color: 'white',
        textAlign: 'left',
        margin: '1% 1%',
        //backgroundColor: '#0000FF88',
    },
    contentBackBoard: {
        position: 'absolute',
        left: '0%',
        top: '0%',
        width: '100%',
        height: '100%',
        backgroundColor: '#00000088',
        margin: '0'
    },
    skipGuide: {
        position: 'absolute',
        right: '1%',
        top: '1%',
        color: 'white',
        //backgroundColor: '#FF000088',
    },
    centerPlayButton: {
        position: 'absolute',
        width: '50%',
        height: '50%',
        left: '25%',
        top: '40%',
        display: 'none',
        opacity: '255'
    },
    contentsScreenMirror: {
        position: 'absolute',
        width: '15%',
        height: '15%',
        left: '70%',
        top: '70%'
    },
    centerPlayButtonText: {
        position: 'absolute',
        width: '100%',
        top: '70%',
        left: '0',
        fontSize: '60%',
        margin: '5%',
        color: 'white'
    },
    playButton: {
        position: 'absolute',
        width: '50%',
        height: '50%',
        left: '0%',
        top: '40%',
        //margin: 'auto',
        display: 'none',
        opacity: '255'
    },
    skipButton: {
        position: 'absolute',
        width: '50%',
        height: '50%',
        left: '50%',
        top: '40%',
        //margin: 'auto',
        display: 'none'
    },
    largeIcon: {
        fontSize: "2em",
        color: green[50]
    }
}));

var _ContentsSkipGuide = '터치하여 정지/스킵 가능';
export const VideoPlay = ({ title, videoUrl, enableContentsSkip, enableCountDown, 
    enableContentsPause, onVideoPlayComplete, onVideoSkip, autoPlay, controls,
    enableTitleText, SetMaxTime, SetCurrentTime, enableFullScreen, enableScreenMirror }) => {
    //console.log('VideoPlay component start : ' + title);
    const classes = useStyles();
    const playButtonRef = useRef(null);
    const skipButtonRef = useRef(null);
    const videoRef = useRef(null);
    const backboardRef = useRef(null);

    useEffect(() => {
        console.log('VideoPlay start:' + videoUrl);
        showPauseButton(!autoPlay);
        var id = undefined;
        if (true == enableCountDown) {
            id = setInterval(timer, 1000);
        }
        return () => {
            if (undefined != id) {
                clearInterval(id);
            }
            console.log('VideoPlay finish:' + videoUrl);
        }
    }, []);

    function timer() {
        const video = videoRef.current;
        if (undefined == video) {
            console.log('videoRef null');
            return;
        }
        //var leftTime = video.duration - video.currentTime;
        SetCurrentTime(parseInt(video.currentTime));
        SetMaxTime(parseInt(video.duration));
    }

    const onVideoClick = () => {
        console.log('onVideoClick call');
        const video = videoRef.current;
        if (undefined == video) {
            console.log('videoRef null');
            return;
        }

        if (enableContentsPause == false) {
            if (undefined != video) {
                video.play();
            }
            console.log('disable contents skip');
            return;
        }

        if (video.paused) {
            video.play();
            showPauseButton(false);
        } else {
            video.pause();
            showPauseButton(true);
        }
    }

    const showPauseButton = (state) => {
        var displayState = "none";
        if (true == state) {
            displayState = "block";
        }
        if (undefined != playButtonRef.current) {
            playButtonRef.current.style.display = displayState;
        }
        if (undefined != skipButtonRef.current) {
            skipButtonRef.current.style.display = displayState;
        }
        if (undefined != backboardRef.current) {
            backboardRef.current.style.display = displayState;
        }
    }

    const onVideoEnded = () => {
        console.log('onVideoEnded call');
        if (undefined != onVideoPlayComplete) {
            onVideoPlayComplete();
        }
    }

    const onPlayClick = () => {
        console.log('onPlayClick call');
        const video = videoRef.current;
        if (undefined == video) {
            console.log('video null');
            return;
        }
        if (undefined != video) {
            video.play();
        }
        showPauseButton(false);
    }

    const onSkipClick = () => {
        console.log('onSkipClick call');
        showPauseButton(false);
        if (undefined != onVideoSkip) {
            onVideoSkip();
        }
    }

    const onScreenMirror = () => {
        EnableScreenMirror();
    }

    const getPauseView = () => {
        if (false == enableContentsPause) {
            return undefined;
        }
        if (true == enableContentsSkip) {
            return (
                <div>
                    <div className={classes.contentBackBoard} ref={backboardRef}></div>
                    <span className={classes.skipGuide}>{_ContentsSkipGuide}</span>
                    <div className={classes.playButton} ref={playButtonRef}>
                        <IconButton aria-label="play" onClick={onPlayClick} >
                            <PlayArrowIcon className={classes.largeIcon} />
                            <span className={classes.centerPlayButtonText}>재생</span>
                        </IconButton>
                    </div>
                    <div className={classes.skipButton} ref={skipButtonRef}>
                        <IconButton aria-label="skip" onClick={onSkipClick}>
                            <SkipNextIcon className={classes.largeIcon} />
                            <span className={classes.centerPlayButtonText}>다음</span>
                        </IconButton>
                    </div>
                </div>
            )
        } else {
            return (
                <div>
                    <div className={classes.contentBackBoard} ref={backboardRef}></div>
                    <div className={classes.centerPlayButton} ref={playButtonRef}>
                        <IconButton aria-label="play" onClick={onPlayClick} >
                            <PlayArrowIcon className={classes.largeIcon} ></PlayArrowIcon>
                            <span className={classes.centerPlayButtonText}>재생</span>
                        </IconButton>
                    </div>
                </div>
            )
        }
    }

    var rootClass = enableFullScreen == false ? classes.root : classes.rootFullScreen;
    var videoClass = enableFullScreen == false ? classes.video : classes.videoFullScreen;

    return (
        <div className={rootClass} style={{ zIndex: '-2' }}>
            <video className={videoClass} onClick={onVideoClick} autoPlay={autoPlay} onEnded={onVideoEnded}  ref={videoRef} controls={controls} playsinline >
                <source src={videoUrl} type="video/mp4" />
                Your browser does not support the video tag.
            </video>
            {(enableTitleText == true) ?
                <span className={classes.contentsTitle}>{title}</span> :
                undefined}
            {getPauseView()}
            {(enableScreenMirror == true) ?
                <img onClick={onScreenMirror} className={classes.contentsScreenMirror} src={process.env.PUBLIC_URL + '/img/screen-mirror-icon.png'} /> : undefined}
        </div>
    );
}


const mapStateToProps = (state) => ({
});
const mapDispatchToProps = { SetMaxTime, SetCurrentTime };

export default connect(mapStateToProps, mapDispatchToProps)(VideoPlay);

