import { SETMAXTIME, SETCURRENTTIME } from '../actions/countdown'

const initialState = {
    _currentTime: 0,
    _maxTime: 0
};

export default function countdown(state = initialState, action) {
    switch(action.type) {
        case SETMAXTIME:
            //console.log('SETMAXTIME:' + action._maxTime);
            return Object.assign({}, state, {
                _maxTime: action._maxTime
            });
        case SETCURRENTTIME:
            //console.log('SETCURRENTTIME:' + action._currentTime);
            return Object.assign({}, state, {
                _currentTime: action._currentTime
            });
        default:
            return state;
    }
}



