import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { makeStyles } from '@material-ui/core/styles';
import { SetSubScene } from '../actions/scene';

import { _contents } from './ContentsResultData'
import './FeedbackScene.css';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxHeight: '100%',
    },
    videoRoot: {
        position: 'relative',
        top: '5%',
        height: '95%',
        width: '100%',
    },
    videoContainer: {
        position: 'relative',
        width: '100%',
        margin: '5px'
    },
    video: {
        position: 'relative',
        width: '100%',
        margin: '0',
    },
    divLine: {
        border: "1px solid",
        color: "#DDDDDD",
        width: "95%"
    }
}));

var _contentsTitle = '마스터 코멘트';

export const FeedbackScene = ({resultState}) => {
    const classes = useStyles();

    useEffect(() => {
    });

    const getConmentsText = (comments) => {
        if (undefined == comments) {
            return undefined;
        }

        let contents = [];
        var count = parseInt(comments.length / 2);
        for (var i = 0; i < count; i++) {
            if (i != 0) {
                contents.push(<br key={comments} />);
            }
            var targetIndex = i * 2;
            var time = comments[targetIndex];
            var comment = comments[targetIndex + 1];
            contents.push(<span key={time + comment + '0'} className='contentsTitle_success_time'>{time} </span>);
            contents.push(<span key={time + comment + '1'} className='contentsTitle_success_text'>{comment}</span>);
        }
        return contents;
    }

    const getRecordSuccuss = (desc, url, comments) => {
        return (
            <div key={desc}>
                <div className='contentsTitle_left'>
                    <span className='contentsTitle_success_text'>{desc}
                    </span>
                </div>
                <video className={classes.video} autoPlay={false} controls={true} poster={process.env.PUBLIC_URL + '/img/HomeTrainingQuest_icon.png'}>
                    <source src={url} type="video/mp4" id="SelectSoundOutput" />
                    Your browser does not support the video tag.
                </video>
                <div className='contentsTitle_left'>
                    {getConmentsText(comments)}
                    {/* <span className='contentsTitle_success_time'>3:15
                    </span>
                
                    <span className='contentsTitle_success_text'>asdfasdfasdf
                    </span> */}
                </div>
                <hr className={classes.divLine} />
            </div>
        )
    }

    const getResultSummery = () => {
        if (resultState) {
            return (
                <div>
                    약간의 동작 교정이 필요하지만, 잘하셨습니다,
                    <br />
                    클리어 도장 찍어드립니다!
                </div>
            );
        } else {
            return (
                <div>
                    아쉽습니다.
                    <br />
                    코멘트를 읽어보시고 다시 도전하세요.
                </div>
            );
        }
    }

    const getVideoList = () => {
        let contents = [];
        Object.keys(_contents).forEach(function (k, v) {
            contents.push(getRecordSuccuss(_contents[k].desc, _contents[k].url, _contents[k].comments));
        });
        return contents;
    }

    return (
        <div key='FeedbackScene' className={classes.root}>
            <div className='contentsSummery'>
                <span className='contentsSummery_title'>{_contentsTitle}</span>
                <div className='contentsSummery_result'>
                    {getResultSummery()}
                </div>
                <hr className={classes.divLine} />
                <div className={classes.videoRoot}>
                    <div className={classes.videoContainer}>
                        {getVideoList()}
                    </div>
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
                _currentSubScene: state.scene._currentSubScene,
    _invalidPosenetList: state.posenet._invalidPosenetList,
    _validPosenetList: state.posenet._validPosenetList,
});
const mapDispatchToProps = { SetSubScene};

export default connect(mapStateToProps, mapDispatchToProps)(FeedbackScene);
