import { SETPOSENETPOSE, CLOSEPOSENET, INVALIDPOSENET, INVALIDPOSENET_CLEAR, VALIDPOSENET, SETCAMERARECORD } from '../actions/posenet'

const initialState = {
    _posenetPose: undefined,
    _closePosenet: '',
    _invalidPosenetList:[],
    _validPosenetList:[],
    _recordState: false
};

export default function posenet(state = initialState, action) {
    //console.log('reducer posenet action call:' + action.type);
    switch(action.type) {
        case SETPOSENETPOSE:
            //console.log('SETPOSENETPOSE:');
            //console.log(action._posenetPose);
            return Object.assign({}, state, {
                _posenetPose: action._posenetPose
            });
        case CLOSEPOSENET:
            console.log('CLOSEPOSENET:' + action._closePosenet);
            return Object.assign({}, state, {
                _closePosenet: action._closePosenet
            });
        case INVALIDPOSENET:
            console.log('INVALIDPOSENET:');
            console.log(state._invalidPosenetList);
            return {
                ...state,
                _invalidPosenetList: [...state._invalidPosenetList, action._invalidPoseKey]
            };
        case INVALIDPOSENET_CLEAR:
            console.log('INVALIDPOSENET_CLEAR:');
            console.log(state._invalidPosenetList);
            return {
                ...state,
                _invalidPosenetList: []
            };
        case VALIDPOSENET:
            console.log('VALIDPOSENET:');
            console.log(state._validPosenetList);
            return {
                ...state,
                _validPosenetList: [...state._validPosenetList, action._validPoseKey]
            }
        case SETCAMERARECORD:
            console.log('SETCAMERARECORD:' + action._recordState);
            return Object.assign({}, state, {
                _recordState: action._recordState
            });
        default:
            return state;
    }
}