export function SetIframeFinish(success) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.parent) {
        return;
    }

    if (success == false) {
        window.parent.postMessage("onBack", "*");
    }
    else {
        window.parent.postMessage("onSuccess", "*");
    }
}

export function SetIframeFullScreen(fullScreen) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.parent) {
        return;
    }
    
    if (fullScreen == false) {
        window.parent.postMessage("onFitScreen", "*");
    }
    else {
        window.parent.postMessage("onFullScreen", "*");
    }
}

export function SetLandscapeMode(fullScreen) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.SetLandscapeMode(fullScreen);
}

export function EnableScreenMirror() {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.EnablingWiFiDisplay();
}

