import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { setScene } from '../actions/scene';
import { InvalidPosenetClear, SetCameraRecord } from '../actions/posenet';

import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import "react-circular-progressbar/dist/styles.css";

import ResultFailure from '../components/ResultFailure';
import ResultSuccess from '../components/ResultSuccess';

import { makeStyles } from '@material-ui/core/styles';
import { VideoRecord_InvalidDataClear } from './../components/utils/VideoRecord';

import VideoPlay from '../components/videoPlay'
import PoseCameraRecord from '../components/PoseCameraRecord'

import { _contents } from './ContentsData'


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxHeight: '100%',
  },
  videoContainer: {
    width: '100%',
    position: 'relative',
  },
  progressContainer: {
    position: 'absolute',
    zIndex: '1',
    left: '2%',
    top: '20%',
    width: '15%',
    height: '15%',
    margin: '0'
},
}));

// TV 연결 기능 아이콘 추가
// 로컬에 영상 저장
// 레디, 셋, 고 영상에 넣기
var _debugStartStep = 6
var _lastCameraRecordStep = 0;
var _recordResultDelayDuration = 2000;
export const ContentsScene = ({ _posenetPose, setScene, InvalidPosenetClear, SetCameraRecord, _invalidPosenetList, _validPosenetList, _currentTime, _maxTime }) => {
  const classes = useStyles();
  const [_step, setStep] = React.useState(_debugStartStep);

  const getStepContents = () => {
    let contents = [];
    var contentsData = getStepData(_step);
    //console.log(contentsData.title);
    if (undefined != contentsData) {
      contents.push(
        createVideoElement(contentsData.title, contentsData.url, contentsData.enableContentsPause)
      );
      
      // 마지막 데이터일 경우 결과화면
      var nextContentsData = getStepData(_step+1);
      if (undefined == nextContentsData) {
        contents.push(getResultScene());
      }

      if (contentsData.enablePlayerView == true) {
        // 카메라 레코딩을 한번만 호출하기 위해서
        if (_lastCameraRecordStep != _step) {
          SetCameraRecord(true);
          _lastCameraRecordStep = _step;
        }
        contents.push(getPoseNetMiniCamera());
      }
    }
    return contents;
  }

  const getStepData = (step) => {
    return _contents['step' + step];
  }

  const createVideoElement = (title, url, enableContentsPause) => {
    return (<VideoPlay key={url} title={title} enableContentsSkip={true} enableContentsPause={enableContentsPause} enableCountDown={true}
    onVideoPlayComplete={onVideoEnded} onVideoSkip={onVideoSkip} videoUrl={url} autoPlay={true} enableTitleText={true} enableFullScreen={true} enableScreenMirror={enableContentsPause} />);
  }

  const onNextStep = () => {
    // 마지막 데이터일 경우 결과화면
    var nextStep = _step + 1;
    var nextContentsData = getStepData(nextStep);
    if (undefined != nextContentsData) {
      changeSceneType(nextStep);
    }
  }

  const onVideoEnded = () => {
    console.log('onVideoEnded call');
    var contentsData = getStepData(_step);
    if (undefined == contentsData) {
      return;
    }

    SetCameraRecord(false);
    if (contentsData.enablePlayerView) {
      setTimeout(function () {
        onNextStep();
      }, _recordResultDelayDuration);
    }
  }

  const onVideoSkip = () => {
    console.log('onVideoSkip call');
    var contentsData = getStepData(_step);
    if (undefined == contentsData) {
      return;
    }     
    onNextStep();
  }

  function changeSceneType(change) {
    var preStep = _step;
    var nextStep = change;

    // 이미 성공한 포즈일 경우 패스
    for (var i = 0; i < _validPosenetList.length; i++) {
      var value = _validPosenetList[i];
      if (value == nextStep || value == nextStep + 1) {
        console.log('skip scene type:' + preStep + '=>' + value);
        changeSceneType(value+1);
        return;
      }
    }
    setStep(nextStep);
    console.log('change scene type:' + preStep + '=>' + nextStep);
  }

  const getPoseNetMiniCamera = () => {
    var titleText = undefined;
    var desc = undefined;
    var contentsData = getStepData(_step);
    if (undefined != contentsData) {
      titleText = contentsData.title;
      desc = contentsData.desc;
    }
    return (
      <div key="PoseNetMiniCamera">
        <PoseCameraRecord step={_step} title={titleText} desc={desc}/>
      </div>
    );
  }

  const onOKClick = () => {
    console.log('onOKClick');
    //setFullScreenResultScene(true);
    setStep(_debugStartStep);
    setScene('Result');
  }

  const onRetryClick = () => {
    console.log('onRetryClick');
    _lastCameraRecordStep = 0;
    InvalidPosenetClear();
    VideoRecord_InvalidDataClear();
    changeSceneType(_debugStartStep);
  }

  const getResultScene = () => {
    //console.log('getResultScene _invalidPlaySet count:' + _invalidPlaySet.length);
    var failureCount = _invalidPosenetList.length;
    if (failureCount == 0) {
      return (
        <div key="ResultSuccess">
          <ResultSuccess onOKClick={onOKClick} />
        </div>
      );
    }
    else {
      return (
        <div key="ResultFailure">
          <ResultFailure failureCount={failureCount} onOKClick={onOKClick} onRetryClick={onRetryClick} />
        </div>
      );
    }
  }

  const getTimer = () => {
    var contentsData = getStepData(_step);
    if (undefined == contentsData) {
      return;
    }
    if (contentsData.enablePlayerView == false) {
      return;
    }

    var leftTime = parseInt(_maxTime - _currentTime);
    return (
      <div className={classes.progressContainer}>
        <CircularProgressbar value={leftTime} maxValue={_maxTime} text={`${leftTime}`}
          styles={buildStyles({
            textColor: "#ffffff",
            pathColor: "#ffffff",
            trailColor: "#ffffff11",
            textSize: '40px'
          })} />
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <div className={classes.videoContainer}>
        {getStepContents()}
        {getTimer()}
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _posenetPose: state.posenet._posenetPose,
  _invalidPosenetList: state.posenet._invalidPosenetList,
  _validPosenetList: state.posenet._validPosenetList,
  _maxTime: state.countdown._maxTime,
  _currentTime: state.countdown._currentTime
});
const mapDispatchToProps = { InvalidPosenetClear, SetCameraRecord, setScene };

export default connect(mapStateToProps, mapDispatchToProps)(ContentsScene);
