import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { setScene } from '../actions/scene';
import { InvalidPosenetClear } from '../actions/posenet';

import ResultFailure from '../components/ResultFailure';
import ResultSuccess from '../components/ResultSuccess';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import { VideoRecord_InvalidDataClear } from './../components/utils/VideoRecord';

import VideoPlay from '../components/videoPlay'
import PoseCameraRecord from '../components/PoseCameraRecord'

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { _contents } from './ContentsData'
import './Contents.css';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    videoContainer: {
        width: '100%',
        position: 'relative',
    },
    contentsTitle: {
        color: 'black'
    },
    BottomBarBack: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '11%',
        top: '89%',
        left: '0%'
    },
    BottomBarButton: {
        position: "fixed",
        height: "8%",
        top: "90%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
    },
    BottomBarButton_text_1: {
        position: 'absolute',
        left: "0",
        top: "10%",
        width: "100%",
        fontSize: '140%',
        fontWeight: 'bold'
    },
    BottomBarButton_text_2: {
        position: 'absolute',
        left: "0",
        top: "60%",
        width: "100%",
        fontSize: '80%',
    }
}));


export const IntroScene = ({ _posenetPose, setScene }) => {
    const classes = useStyles();
    console.log('IntroScene start');

    const createVideoElement = (title, desc, url) => {
        //console.log('createVideoElement ' + title)
        return (
            <div key={title} >
                <span className={classes.contentsTitle}>{desc}</span>
                <div className={classes.videoContainer}>
                    <VideoPlay key={url} title={title} enableContentsSkip={false} enableContentsPause={false} videoUrl={url} autoPlay={false} enableTitleText={false} enableFullScreen={false} enableScreenMirror={false} />
                </div>
            </div>);
    }

    const getContents = () => {
        let contents = [];
        Object.keys(_contents).forEach(function (k, v) {
            if (_contents[k].type == 'set') {
                contents.push(createVideoElement(_contents[k].title, _contents[k].desc,_contents[k].url));
            }
        });
        return contents;
    }

    const onPlay = () => {
        console.log('onPlay call');
        //setScene('Play');
        setScene('BodyScan');
    }

    return (
        <div className={classes.root}>
            <div className='contents_body'>
                <div className='contents_intro'>
                    <span className='.contents_intro_text'>어디스든 가볍게!</span>
                    <span className='contents_intro_text_sub'><br />딱 16분 트레이닝으로 코어를 단단하게 조이고, 복근을 활성화 합니다.<br />따라오세요</span>
                </div>
                {getContents()}
                <br />
                <br />
                <br />
                <div className={classes.BottomBarBack}></div>
            </div>
            <Button onClick={onPlay} className={classes.BottomBarButton} variant="contained" color="secondary">
                    <span className={classes.BottomBarButton_text_1}>영상 기록 시작</span>
                    <span className={classes.BottomBarButton_text_2}>총 11 Set / 15 분</span>
            </Button>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(IntroScene);
