import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { setScene } from '../../actions/scene';

const contentsLoading = {
    position: 'absolute',
    zIndex: '1',
    left: '0%',
    top: '0%',
    width: '100%',
    height: '100%',
    textAlign : 'left',
    margin: '0 auto',
    fontSize: '150%',
    textShadow: '2px 2px 5px black',
    color: 'white',
    //backgroundColor: '#FF000088',
}

const contentsScaning = {
    position: 'absolute',
    zIndex: '1',
    left: '0%',
    top: '0%',
    width: '100%',
    height: '100%',
    textAlign : 'right',
    margin: '0 auto',
    fontSize: '130%',
    textShadow: '2px 2px 5px black',
    color: 'white',
    //backgroundColor: '#0000FF88',
}

const root = {
    position: 'fixed',
    zIndex: '1',
    left: '0%',
    top: '0%',
    width: '100%',
    height: '100%',
    //backgroundColor: '#0000FF88',
}

export const PosenetPoseScan = ({ setScene, _posenetPose, _countdown, minPoseConfidence }) => {
    useEffect(() => {
        //console.log('game countdowntime:' + _countdown);
    });
    const getCameraScan = () => {
        return (
            <span style={contentsLoading}>카메라 인식 중...</span>
        )
    }
    const getCameraInvalid = () => {
        if (undefined == _posenetPose) {
            return undefined;
        }
        if (minPoseConfidence < _posenetPose.score) {
            return undefined;
        }
        return (
            <span style={contentsScaning}>화면 안으로 들어오세요.</span>
        )
    }
    return (
    <div style={root}>
        {getCameraScan()}
        {getCameraInvalid()}
    </div>);
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _countdown: state.countdown._countdown
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(PosenetPoseScan);
