import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import { setScene } from '../actions/scene';

import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import "react-circular-progressbar/dist/styles.css";

import PoseNetCamera from '../components/posenet/PosenetCamera'

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'absolute',
        left: '0%',
        top: '0%',
        width: '100%',
        height: '100%'
    },
    videoContainer: {
        position: 'absolute',
        height: '100%',
        width: '100%',
        left: '0%',
        top: '0%'
    },
    contentsLoading: {
        position: 'absolute',
        zIndex: '1',
        left: '0%',
        top: '0%',
        width: '100%',
        height: '100%',
        textAlign : 'left',
        margin: '0 auto',
        fontSize: '150%',
        textShadow: '2px 2px 5px black',
        color: 'white',
        //backgroundColor: '#FF000088',
    },
    progressContainer: {
        position: 'absolute',
        zIndex: '1',
        left: '0%',
        top: '0%',
        width: '20%',
        height: '20%',
        marginLeft: '5%',
        marginTop: '10%' 
    },
}));

// 풀화면으로 화면안에 들어왔는지 인식
var _minPoseConfidence = 0.3;
var _maxScaneCount = 4;
export const VideoBodyScanScene = ({ _posenetPose, setScene }) => {
    const classes = useStyles();
    const [_step, setStep] = React.useState(0);

    useEffect(() => {
        console.log('_posenetPose effect call');
        if (undefined != _posenetPose) {
            if (_minPoseConfidence <= _posenetPose.score) {
                addScanProgress();
            } else {
                resetScanProgress();
            }
        }
    }, [_posenetPose]);
    const getPoseNetCamera = () => {
        return (
            <div key="PoseNetScanCamera">
                <PoseNetCamera showMiniVideo={false} minPoseConfidence={_minPoseConfidence} />
            </div>
        );
    }
    const addScanProgress = () => {
        setStep(_step+1);
    }
    const resetScanProgress = () => {
        setStep(0);
    }
    if (_step >= _maxScaneCount) {
        setScene('Play');
    }
    return (
        <div className={classes.root}>
            <div className={classes.videoContainer}>
                <div className={classes.progressContainer}>
                    <CircularProgressbar value={_step} maxValue={_maxScaneCount} text={`${_step * (100 / 4)}%`} 
                    styles={buildStyles({
                        textColor: "#ffffff",
                        pathColor: "#ffffff",
                        trailColor: "#ffffff11",
                        textSize: '40px'
                    })} />
                </div>
                <span className={classes.contentsLoading}>카메라 인식 중...</span>
                {getPoseNetCamera()}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(VideoBodyScanScene);
