export const SETMAXTIME = 'SETMAXTIME';
export function SetMaxTime(value) {
    return {
        type: SETMAXTIME,
        _maxTime: value
    };
}

export const SETCURRENTTIME = 'SETCURRENTTIME';
export function SetCurrentTime(value) {
    return {
        type: SETCURRENTTIME,
        _currentTime: value
    };
}

