import React, { Component } from 'react';
import { connect } from 'react-redux';
import './App.css';
import ContentsScene from './scene/ContentsScene';
import FullScreenResult from './components/FullScreenResult';
import IntroScene from './scene/IntroScene';
import VideoBodyScanScene from './scene/VideoBodyScanScene';
import FeedbackScene from './scene/FeedbackScene';
import { SetIframeFullScreen, SetLandscapeMode } from './components/utils/SendMessage'

class App extends Component {

  componentWillUnmount() {
    SetIframeFullScreen(false);
    SetLandscapeMode(false);
  }

  getScene() {
     switch (this.props._currentScene) {
      case 'Result': {
        SetIframeFullScreen(false);
        SetLandscapeMode(false);
        return <FullScreenResult />;
      }
      case 'Play': {
        SetIframeFullScreen(true);
        SetLandscapeMode(true);
        return <ContentsScene />;
      }
      case 'BodyScan': {
        SetIframeFullScreen(true);
        SetLandscapeMode(true);
        return <VideoBodyScanScene />;
      }
      case 'Feedback': {
        SetIframeFullScreen(false);
        SetLandscapeMode(false);
        return <FeedbackScene resultState={false}/>;
      }
      default: {
        SetIframeFullScreen(false);
        SetLandscapeMode(false);
        return <IntroScene />;
      }
    }
  }

  render() {
    return (
      <div className="App">
        {this.getScene()}
      </div>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    _currentScene: state.scene._currentScene
  };
}

let mapDispatchToProps = (dispatch) => {
  return {
  }
}

App = connect(mapStateToProps, mapDispatchToProps)(App);

export default App;