import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import PoseNetCamera from '../components/posenet/PosenetCamera'
import PosenetPoseScan from '../components/PoseNetGame/PosenetPoseScan'

import { makeStyles } from '@material-ui/core/styles';
import { SetSubScene } from '../actions/scene';
import { InvalidPosenet, ValidPosenet, ClosePosenet } from '../actions/posenet';

import { VideoRecord_Start, VideoRecord_Pause, VideoRecord_Stop, ValidVideoRecord, VideoRecord_Download, GetVideoRecordCount} from './../components/utils/VideoRecord'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxHeight: '100%',
    },
    contentsTitle: {
        position: 'absolute',
        left: '5%',
        top: '5%',
        fontSize: '250%',
        textShadow: '2px 2px 5px black',
        color: 'red'
    },
    contentsResult: {
        position: 'fixed',
        left: '0%',
        top: '10%',
        width: '100%',
        height: 'auto',
        fontSize: '250%',
        textShadow: '2px 2px 5px black',
        textAlign: 'center',
        color: 'white'
    }
}));

var _poseValidPercent = 50;
var _minPoseConfidence = 0.3;
var _poseDetectionInvalidCount = 0;
var _poseDetectionValidCount = 0;
var _step = undefined;
var _title = undefined;
var _desc = undefined;
export const PoseCameraRecord = ({ SetSubScene, _currentSubScene, InvalidPosenet, ValidPosenet, _invalidPosenetList, _validPosenetList, _posenetPose, step, title, desc, _recordState }) => {
    const classes = useStyles();

    useEffect(() => {
        if (undefined != _posenetPose) {
            //console.log('pose score valid count:' + _poseDetectionValidCount + ", invalid count:" + _poseDetectionInvalidCount);
            if (_minPoseConfidence < _posenetPose.score) {
                _poseDetectionValidCount++;
            }
            else {
                _poseDetectionInvalidCount++;
            }
            // 카메라 로딩이 느려 주기적인 체크 필요
            console.log('PoseCameraRecord _recordState:' + _recordState);
            if (_recordState == true) {
                startVideoRecord();
            } else {
                stopVideoRecord();
            }
        }
    }, [_posenetPose]);

    useEffect(() => {
        console.log('PoseCameraRecord start:' + step);
        _poseDetectionInvalidCount = 0;
        _poseDetectionValidCount = 0;
        _step = step;
        _title = title;
        _desc = desc;
        return () => {
            stopVideoRecord();
        }
    }, []);

    const startVideoRecord = () => {
        var videoElement = document.getElementById('videoCamera');
        if (undefined != videoElement) {
            VideoRecord_Start(videoElement.srcObject);
        }
    }

    const stopVideoRecord = () => {
        console.log(' stopVideoRecord call');
        if (ValidVideoRecord() == false) {
            return;
        }
        var currentPercent = 0;
        var totalCount = _poseDetectionValidCount + _poseDetectionInvalidCount;
        if (totalCount != 0) {
            currentPercent = _poseDetectionValidCount * 100 / (_poseDetectionValidCount + _poseDetectionInvalidCount);
        }
        console.log('percent:' + currentPercent + ', total:' + totalCount);
        var validState = (_poseValidPercent < currentPercent);
        if (false == validState) {
            InvalidPosenet(step);
        } else {
            ValidPosenet(step);
        }
        VideoRecord_Stop(validState, _step, _title, _desc);
    }

    const getResultText = () => {
        if (_recordState == false) {
            console.log(_validPosenetList);
            console.log(_invalidPosenetList);

            for (var i = 0; i < _validPosenetList.length; i++) {
                var value = _validPosenetList[i];
                if (_step == value) {
                    return (
                        <span className={classes.contentsResult}>기록 성공</span>
                    );
                }
            }
            for (var i = 0; i < _invalidPosenetList.length; i++) {
                var value = _invalidPosenetList[i];
                if (_step == value) {
                    return (
                        <span className={classes.contentsResult}>기록 실패</span>
                    );
                }
            }
        }
        return undefined;
    }

    return (
        <div key="PoseNetMiniCamera">
            <PoseNetCamera showMiniVideo={true} minPoseConfidence='0.1'>
                {/* <PosenetPoseScan minPoseConfidence={_minPoseConfidence} /> */}
            </PoseNetCamera>
            {getResultText()}
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _currentSubScene: state.scene._currentSubScene,
    _recordState: state.posenet._recordState,
    _invalidPosenetList: state.posenet._invalidPosenetList,
    _validPosenetList: state.posenet._validPosenetList
});
const mapDispatchToProps = { SetSubScene, InvalidPosenet, ValidPosenet, ClosePosenet };

export default connect(mapStateToProps, mapDispatchToProps)(PoseCameraRecord);
